<template>
  <div class="container">
    <div class="card" style="width:23%;">
      <div class="title">用户总体角度</div>
      <div class="panelContainer">
        <div class="tabContainer">
          <div v-for="(tab, index) in dataPanels.slice(0,3)" :key="tab.title" class="tab">
            <div class="tabTitle">{{ tab.title }}</div>
            <div class="data">{{ tab.data }} <span v-if="index!==2">人</span></div>
          </div>
        </div>
        <div class="tabContainer">
          <div v-for="(tab, index) in dataPanels.slice(3,6)" :key="tab.title" class="tab">
            <div class="tabTitle">{{ tab.title }}</div>
            <div class="data">{{ tab.data }}
              <span v-if="index===0">人次</span>
              <span v-if="index===1">人</span>
            </div>
          </div>
        </div>
        <div class="tabContainer">
          <div v-for="(tab, index) in dataPanels.slice(6,9)" :key="tab.title" class="tab">
            <div class="tabTitle">{{ tab.title }}</div>
            <div class="data">{{ tab.data }}
              <span v-if="index===2">份</span>
              <span v-else>次</span>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="card" style="width:75%;">
      <div class="title">用户画像</div>
      <div class="panelContainer pieChart">
        <div ref="degreeChart" style="width: 300px; height: 180px;"></div>
        <div ref="school985211Chart" style="width: 300px; height: 180px;"></div>
        <div ref="qsChart" style="width:300px; height: 180px;"></div>
        <div ref="jobTypeChart" style="width:300px; height: 180px;"></div>
      </div>
    </div>
    <div class="card" style="width: 100%;">
      <div class="panelContainer">
        <div class="title">新用户角度</div>
        <div class="dataSelect">
          <div class="block">
            <el-date-picker
                v-model="dateRange"
                type="daterange"
                unlink-panels
                range-separator="至"
                start-placeholder="开始日期"
                end-placeholder="结束日期"
                size="small"
                :shortcuts="shortcuts"
            />
          </div>
        </div>
      </div>
      <div class="panelContainer">
        <div ref="barChart" style="width: 60%; height: 300px;"></div>
        <div ref="lineChart" style="width: 40%; height: 300px;"></div>
      </div>
    </div>
    <div class="card" style="width: 100%;">
      <div class="panelContainer">
        <div class="title">岗位角度</div>
        <div class="dataSelect">
          <div class="block">
            <el-date-picker
                v-model="dateRangeJd"
                type="daterange"
                unlink-panels
                range-separator="至"
                start-placeholder="开始日期"
                end-placeholder="结束日期"
                size="small"
                :shortcuts="jdShortcuts"
            />
          </div>
          <div class="block">
            <div>
              <el-radio-group v-model="radio" size="small">
                <el-radio-button label="周纬度" />
                <el-radio-button label="月纬度" />
              </el-radio-group>
            </div>
          </div>
        </div>
      </div>
      <div class="panelContainer">
        <div ref="barChartJd" style="width: 60%; height: 300px;"></div>
        <div ref="lineChartJd" style="width: 40%; height: 300px;"></div>
      </div>
    </div>
  </div>
</template>


<script>
import * as echarts from 'echarts';
import {getApiRootPath} from "@/utils/url"
import axios from 'axios';
import {reactive, onMounted, computed, ref, watch} from "vue"
import {ElNotification} from "element-plus"
import qs from "qs"
import dayjs from "dayjs"
const baseUrl = getApiRootPath()
const FORMAT = 'YYYY-MM-DD'
const apiUrl = [
  `${baseUrl}/api/data/user`,
  `${baseUrl}/api/data/user_upload`,
  '无',
  `${baseUrl}/api/data/submit`,
  `${baseUrl}/api/data/user_submit`,
  `${baseUrl}/api/data/user_submit_rate`,
  `${baseUrl}/api/data/user_average_submit`,
  `${baseUrl}/api/data/user_median_submit`,
  `${baseUrl}/api/data/user_resume`,
]

export default {
  setup() {
    const degreeChart = ref(null);
    const school985211Chart = ref(null);
    const qsChart = ref(null);
    const jobTypeChart = ref(null);
    const barChart = ref(null);
    const lineChart = ref(null);
    const barChartJd = ref(null);
    const lineChartJd = ref(null);
    const radio = ref('周纬度')
    const dataPanels = reactive([
      {title: '用户总数', data: ''},
      {title: '上传简历用户数', data: ''},
      {title: '简历上传率', data: ''},
      {title: '总投递量', data: ''},
      {title: '有投递用户数', data: ''},
      {title: '投递率', data: ''},
      {title: '人均投递量', data: ''},
      {title: '中位数投递量', data: ''},
      {title: '简历总数', data: ''},
    ]);
    const dateRange = ref([])
    const dateRangeJd = ref([])

    const shortcuts = [
      {
        text: '近一周',
        value: () => {
          const end = new Date()
          const start = new Date()
          start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
          return [start, end]
        },
      },
      {
        text: '近一个月',
        value: () => {
          const end = new Date()
          const start = new Date()
          start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
          return [start, end]
        },
      },
      {
        text: '近三个月',
        value: () => {
          const end = new Date()
          const start = new Date()
          start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
          return [start, end]
        },
      },
    ]
    const jdShortcuts = [
      {
        text: '近一个月',
        value: () => {
          const end = new Date()
          const start = new Date()
          start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
          return [start, end]
        },
      },
      {
        text: '近三个月',
        value: () => {
          const end = new Date()
          const start = new Date()
          start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
          return [start, end]
        },
      },
      {
        text: '近半年',
        value: () => {
          const end = new Date()
          const start = new Date()
          start.setTime(start.getTime() - 3600 * 1000 * 24 * 90 * 2)
          return [start, end]
        },
      },
    ]
    watch(dateRange, () => {
      // renderBarChart();
      prepareChartData()
    });
    watch(dateRangeJd, () => {
      // renderBarChart();
      prepareJdChartData()
    });
    watch(radio,()=>{
      prepareJdChartData()
    })
    onMounted(() => {
      fetchData();
      fetchPieData();
      prepareChartData();
      prepareJdChartData();
      dataPanels[2].data = resume_upload_rate
      dataPanels[5].data = resume_submit_rate
    });
    const resume_upload_rate = computed(() => {
      if (!dataPanels[0].data || !dataPanels[1].data) {
        return '加载中';
      }
      return (dataPanels[1].data / dataPanels[0].data * 100).toPrecision(4) + "%"
    });
    const resume_submit_rate = computed(() => {
      if (!dataPanels[1].data || !dataPanels[4].data) {
        return '加载中';
      }
      return (dataPanels[4].data / dataPanels[1].data * 100).toPrecision(4) + "%"
    });
    const fetchData = () => {
      dataPanels.forEach((tab, index) => {
        if (index === 2 || index === 5) {
          return
        }
        console.log(index)
        axios.get(apiUrl[index])
            .then(res => {
              const {status, data, message} = res.data
              if (status === '0') {
                if (data && data.length) {
                  tab.data = data[0].count;
                }
              } else {
                ElNotification({
                  title: "查询失败",
                  message,
                  type: "warning",
                });
              }
            })
            .catch(error => {
              console.error(error);
            });
      });

    }
    const prepareChartData = async() => {
        axios({
          method: "post",
          url: `${baseUrl}/api/data/user_by_time`,
          data: qs.stringify({
            // startTime: "2022-12-27 00:00:00",
            // endTime: "2023-12-28 00:00:00",
            startTime:dayjs(dateRange.value[0]).format(FORMAT),
            endTime:dayjs(dateRange.value[1]).format(FORMAT),
          }),
          headers: {
            "Content-Type": "application/x-www-form-urlencoded"
          }
        }).then((res) => {
          const {status, data, message} = res.data
          if (status === '0') {
            if (data && data.length) {
              const dates = data.map(item => dayjs(item.date).format(FORMAT));
              const newUsers = data.map(item => item.new_user);
              const newUserSubmit = data.map(item => item.new_user_submit);
              const newUserUpload = data.map(item => item.user_upload);
              const submitRate = data.map(item => item.new_user_submit_rate);
              const uploadRate = data.map(item => item.resume_upload_rate);

              renderBarChart(dates, newUsers, newUserUpload, newUserSubmit);
              renderLineChart(dates, submitRate, uploadRate);
            }
          } else {
            ElNotification({
              title: "查询失败",
              message,
              type: "warning",
            })
          }
        }).catch(error => {
          console.error(error)
        });
      };
    const prepareJdChartData = async() => {
      axios({
        method: "post",
        url: radio.value ===  '周纬度' ? `${baseUrl}/api/data/jd_data_by_week` : `${baseUrl}/api/data/jd_data_by_month`,
        data: qs.stringify({
          startTime:dayjs(dateRangeJd.value[0]).format(FORMAT),
          endTime:dayjs(dateRangeJd.value[1]).format(FORMAT),
        }),
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      }).then((res) => {
        const {status, data, message} = res.data
        if (status === '0') {
          if (data && data.length) {
            renderJdBarChart(data);
          }
        } else {
          ElNotification({
            title: "查询失败",
            message,
            type: "warning",
          })
        }
      }).catch(error => {
        console.error(error)
      });
    };

    const fetchPieData = async () => {
      await axios.get(`${baseUrl}/api/data/user_job_type`).then(res => {
        const {status, data, message} = res.data
        if (status === '0') {
          if (data && data.length) {
            renderPieChart2(data)
          }
        } else {
          ElNotification({
            title: "查询失败",
            message,
            type: "warning",
          });
        }
      }).catch(error => {
        console.error(error);
      });
      await axios.get(`${baseUrl}/api/data/user_education`).then(res => {
        const {status, data, message} = res.data
        if (status === '0') {
          if (data && data.length) {
            const {user, big, bachelor, master, school_985, school_211, QS_top_200, QS_top_200_CN}  = data[0]
            renderPieChart(user, big, bachelor, master, school_985, school_211, QS_top_200, QS_top_200_CN)
          }
        } else {
          ElNotification({
            title: "查询失败",
            message,
            type: "warning",
          });
        }
      }).catch(error => {
        console.error(error);
      });
    };

    const renderPieChart2 = (data) => {
      const jobTypeChartInstance = echarts.init(jobTypeChart.value);
      const option = {
        title:{
          show:true,
          text:'用户比例',
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b}: {c} ({d}%)',
        },
        series:[{
            name: '用户比例',
            type: 'pie',
            radius: '60%',
            center: ['50%', '60%'],
            data,
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)',
              },
            },
          }]
      };
      jobTypeChartInstance.setOption(option);
    }
    const renderPieChart = (user, big, bachelor, master, school_985, school_211, QS_top_200, QS_top_200_CN) => {
        const degreeChartInstance = echarts.init(degreeChart.value);
        const school985211ChartInstance = echarts.init(school985211Chart.value);
        const qsChartInstance = echarts.init(qsChart.value);

        const option = {
          title:{
            show:true,
          },
          legend: {
            y: 'bottom',  //图例上下居
          },
          tooltip: {
            trigger: 'item',
            formatter: '{a} <br/>{b}: {c} ({d}%)',
          },
        };
      option.title.text = '学历'
      option.series =  [
        {
          name: '学历',
          type: 'pie',
          radius: '50%',
          center: ['50%', '50%'],
          data: [
            { value: big, name: '大专' },
            { value: bachelor, name: '本科' },
            { value: master, name: '硕士' },
            { value: user - bachelor - master, name: '其他' },
          ],
          emphasis: {
            itemStyle: {
              shadowBlur: 10,
              shadowOffsetX: 0,
              shadowColor: 'rgba(0, 0, 0, 0.5)',
            },
          },
        }]
      degreeChartInstance.setOption(option);
      option.title.text = '学校'
      option.series = [{
          name: '985&211率',
          type: 'pie',
          radius: '50%',
          center: ['50%', '50%'],
          data: [
            { value: school_985, name: '985' },
            { value: school_211, name: '211' },
            { value: QS_top_200 - QS_top_200_CN, name: '海外' },
            { value: user - school_211 - school_985 - QS_top_200 + QS_top_200_CN, name: '其他' },
          ],
          emphasis: {
            itemStyle: {
              shadowBlur: 10,
              shadowOffsetX: 0,
              shadowColor: 'rgba(0, 0, 0, 0.5)',
            },
          },
        }]
      school985211ChartInstance.setOption(option);
      option.series = [{ name: 'QS前200率',
          type: 'pie',
          radius: '50%',
          center: ['50%', '50%'],
          data: [
            { value: QS_top_200 - QS_top_200_CN, name: '海外QS前200' },
            { value: QS_top_200_CN, name: '国内QS前200' },
            { value: user - QS_top_200, name: '其他' },
          ],
          emphasis: {
            itemStyle: {
              shadowBlur: 10,
              shadowOffsetX: 0,
              shadowColor: 'rgba(0, 0, 0, 0.5)',
            },
          },
        }]
      option.title.text = '学校QS'
      qsChartInstance.setOption(option);
    };
    const renderBarChart = (dates, newUsers, userUploads, newUserSubmit) => {
      const chart = echarts.init(barChart.value);
      const option = {
        tooltip: {
          trigger: 'axis',
        },
        legend: {
          data: ['产生投递的新用户', '上传简历的新用户', '新用户总数'],
        },
        xAxis: {
          type: 'category',
          data: dates,
        },
        yAxis: {
          type: 'value',
        },
        series: [
          {
            name: '产生投递的新用户',
            type: 'bar',
            data: newUserSubmit,
          },
          {
            name: '上传简历的新用户',
            type: 'bar',
            data: userUploads,
          },
          {
            name: '新用户总数',
            type: 'bar',
            data: newUsers,
          },
        ],
        grid: {
          x: 40,
          y: 20,
          x2: 40,
          y2: 20
        },
      };
      chart.setOption(option);
    };
    const renderLineChart = (dates, newUsers, userUploads) => {
        const chart = echarts.init(lineChart.value);
        const option = {
          tooltip: {
            trigger: 'axis',
            formatter: function (params) {
              var html = params[0].name + "<br>";
              for (var i = 0; i < params.length; i++) {
                html += params[i].marker + params[i].seriesName + ":&nbsp&nbsp&nbsp&nbsp<b>" + (params[i].value * 100).toPrecision(3);
                html += "%</b>";
                if (i !== params.length - 1) html += "<br>";
              }
              return html + "<br>";
            }
          },
          legend: {
            data: ['新用户投递率', '新用户上传率'],
          },
          xAxis: {
            type: 'category',
            data: dates,
          },
          yAxis: {
            type: 'value',
            axisLabel: {
              formatter: function (value) {
                console.log(value)
                return Math.floor(value * 100) + '%'
              }
            },
          },
          series: [
            {
              name: '新用户投递率',
              type: 'line',
              data: newUsers,
            },
            {
              name: '新用户上传率',
              type: 'line',
              data: userUploads,
            },
          ],
          grid: {
            x: 40,
            y: 20,
            x2: 40,
            y2: 20
          },
        };
        chart.setOption(option);
      };
    const renderJdBarChart = (data)=>{
      const chart = echarts.init(barChartJd.value);
      const lineChart = echarts.init(lineChartJd.value);

      const timeList = [...new Set(data.map(item => item.time))];
      const jobTypes = [...new Set(data.map(item => item.jobType))];
      // 在数据中插入占位数据
      data = insertPlaceholderData(data, timeList, jobTypes);

      const barOptions = {
        title: {
          text: '岗位投递量'
        },
        tooltip:{
          trigger: 'axis',
        },
        legend: {
          data: jobTypes,
          y: 'bottom',  //图例上下居
        },
        xAxis: {
          type: 'category',
          data: timeList
        },
        yAxis: {
          type: 'value'
        },
        series: jobTypes.map(jobType => {
          return {
            name: jobType,
            type: 'bar',
            data: data.filter(item => item.jobType === jobType).map(item => item.total_count)
          };
        })
      };
      chart.setOption(barOptions);

      const lineOptions = {
        title: {
          text: '平均投递比'
        },
        tooltip: {
          trigger: 'axis',
          formatter: function (params) {
            var html = params[0].name + "<br>";
            for (var i = 0; i < params.length; i++) {
              html += params[i].marker + params[i].seriesName + ":&nbsp&nbsp&nbsp&nbsp<b>" + (params[i].value * 100).toPrecision(3);
              html += "%</b>";
              if (i !== params.length - 1) html += "<br>";
            }
            return html + "<br>";
          }
        },
        legend: {
          data: jobTypes,
          y: 'bottom',  //图例上下居
        },
        xAxis: {
          type: 'category',
          data: timeList
        },
        yAxis: {
          type: 'value'
        },
        series: jobTypes.map(jobType => {
          return {
            name: jobType,
            type: 'line',
            // smooth: true,
            data: data.filter(item => item.jobType === jobType).map(item => item.average_delivery_ratio)
          };
        })
      };
      lineChart.setOption(lineOptions);
  }
// // 在数据缺失的时间段插入占位数据函数
//     function insertPlaceholderData(data, missingTime, jobTypes) {
//       const placeholderData = jobTypes.map(jobType => {
//         return {
//           time: missingTime,
//           jobType: jobType,
//           total_jobs: 0,
//           total_count: 0,
//           average_delivery_ratio: 0
//         };
//       });
//       return [...data, ...placeholderData];
//     }
    // 在数据中插入占位数据函数
    function insertPlaceholderData(data, timeList, jobTypes) {
      const newData = [];
      for (const time of timeList) {
        for (const jobType of jobTypes) {
          const existingData = data.find(item => item.time === time && item.jobType === jobType);
          if (existingData) {
            newData.push(existingData);
          } else {
            newData.push({
              time: time,
              jobType: jobType,
              total_jobs: 0,
              total_count: 0,
              average_delivery_ratio: 0
            });
          }
        }
      }
      return newData;
    }

      return {
        dateRange,
        dateRangeJd,
        shortcuts,
        jdShortcuts,
        dataPanels,
        resume_upload_rate,
        barChart,
        lineChart,
        barChartJd,
        lineChartJd,
        degreeChart,
        qsChart,
        school985211Chart,
        jobTypeChart,
        fetchData,
        radio,
      };
  }
};
</script>
<style>
.data{
  font-weight: bold;
}
.container{
  display: flex;
  /*align-items: start;*/
  justify-content: space-between;
  width: 95%;
  flex-wrap: wrap;
}
.panelContainer{
  display: flex;
}
.card{
  display: flex;
  flex-direction: column;
  /*justify-content: start;*/
  justify-content: space-between;
  margin: 5px;
  padding: 10px;
  background: #d4dce8;
  border-radius: 10px;
  font-size: 20px;
  font-weight: bold;
}
.tabContainer{
  display: flex;
  flex-direction: column;
  margin: 0 5px;
}
.title{
  font-size: 24px;
}
.tabTitle{
  color:grey;
  font-size: 16px;
}
.dataSelect {
  margin: 0 20px;
   display: flex;
   padding: 0;
   /*flex-wrap: wrap;*/
 }

.dataSelect .block {
  text-align: center;
  border-right: solid 1px var(--el-border-color);
  flex: 1;
}
.block  .el-range-separator{
  margin: 10px;
}
.pieChart{
  justify-content: center;
}

</style>
